export default {
	data() {
		return {
			id:'',
			order:''
		}
		
	},
	onLoad(options) {
		this.id = options.id
		this.get_order()
	},
	methods:{
		get_order(){
			this.$api.http.post('shop/get_one_order',{id:this.id}).then(res=>{
				this.order = res
			})
		}
	},
	onPullDownRefresh() {
		this.get_order()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
	
}
